
.messages__wrapper{

  display: flex;
  flex-direction: row;
  height: 100%;
  justify-content: space-between;

  .contacts__wrapper{
    background: #fff;
    box-shadow: 0 0 6px #d4d0d0;
    flex: 0.25;

      .contacts_scrollable{
        position: relative;
        height: 510px;
      }

  }

  header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f6f6f8;
    margin: 0 13px;
    text-transform: capitalize;

    h5{
      color: #000;
    }
  }

  .contact{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 15px 0;
    cursor: pointer;
    transition: all .3s ease-in-out;

    &:hover,
    +.active{
      background-color: #f6f6f8;
    }

    .notify{
      background: #f14040;
      width: 20px;
      height: 20px;
      display: block;
      border-radius: 50%;
      text-align: center;
      color: #fff;
    }

    img{
      width: 50px;
      height: 50px;
    }

    .details{

      .name{
        font-size: 20px;
        margin-top: 8px;
        text-transform: capitalize;
      }
      p.last-message{
        font-size: 13px;
        color: #a9a8a8;
        width: 15px;
        width: 145px;
        margin: 0;
      }
    }
  }
}


.messages__board{
  background-color: rgb(249, 249, 249);
  flex: .73;
  position: relative;

  header{
    border-bottom: 1px solid #e6e6e8;
    font-weight: 300;
  }

  footer{
    position: absolute;
    left: 0;
    bottom: 0;
    height: 100px;
    width: 100%;
    box-shadow: 0px -10px 14px rgba(218, 218, 218, 0.41);
    display: flex;
    align-items: center;

    .send_message_wrapper{
      width: 85%;
      margin: 0 auto;
      background: #fff;
      height: 65px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      border-radius: 5px;

      input{
        border:none;
        margin: 0;
        width: 569px;

        &:focus{
          border:none;
          box-shadow: none;
        }
      }

      form{width: 80%;}
    }
  }

  .messages{
     height: 410px;
     position: relative;

     .no-messages{
       height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 30px;
      font-weight: 300;

       i{
         font-size: 30px;
         margin-right: 20px;
       }
     }

     .msg{
       display: flex;
      align-items: center;
      padding: 10px;

      +.me{
          flex-direction: row-reverse;
          .content{
            text-align: right;
            background: #3a84f3;
            color: #fff;
          }
      }

      img{
        width: 50px;
        height: 50px;
      }

      .content{
        background: #fff;
        margin: 15px;
        padding: 10px;
        border-radius: 10px;
        width: 50%;
      }
     }
  }
}
